@font-face {
  font-style: normal;
  font-weight: 400px;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500px;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/.montserrat-medium.woff2") format("woff2"), url("../fonts/.montserrat-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 600px;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/.montserrat-semibold.woff2") format("woff2"), url("../fonts/.montserrat-semibold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700px;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/.montserrat-bold.woff2") format("woff2"), url("../fonts/.montserrat-bold.woff") format("woff"); }

body {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1366px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #011c40;
  background-color: #f9fbfd; }

a {
  display: block;
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header__wrapper {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  box-sizing: border-box;
  width: 1366px;
  padding: 35px 103px;
  background-color: transparent; }
  @media (max-width: 1365px) {
    .page-header__wrapper {
      width: 768px;
      padding: 30px 45px; } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      width: 320px;
      padding: 0; } }

.page-header__main-nav {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between; }

.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__item {
  margin-right: 7px; }
  @media (max-width: 1365px) {
    .main-nav__item {
      margin-right: 13px; } }
  @media (max-width: 767px) {
    .main-nav__item {
      margin: 0; } }

.main-nav__item:nth-last-child(1) {
  margin-right: 0; }

.main-nav__link {
  padding: 13px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd; }
  @media (max-width: 1365px) {
    .main-nav__link {
      padding: 5px;
      font-size: 14px;
      line-height: 17px; } }
  @media (max-width: 767px) {
    .main-nav__link {
      padding: 14px 16px; } }

.main-nav__link--logo {
  padding: 0; }
  @media (max-width: 1365px) {
    .main-nav__link--logo {
      align-self: center;
      padding: 0; } }
  @media (max-width: 767px) {
    .main-nav__link--logo {
      padding: 20px 16px 8px; } }

.main-nav__link--logo img {
  width: 70px;
  height: 38px;
  fill: #f9fbfd; }
  @media (max-width: 1365px) {
    .main-nav__link--logo img {
      width: 38px;
      height: 20px; } }
  @media (max-width: 767px) {
    .main-nav__link--logo img {
      width: 42px;
      height: 22px; } }

.main-nav__toggle {
  display: none; }
  @media (max-width: 767px) {
    .main-nav__toggle {
      position: relative;
      display: block;
      padding: 22px 22px;
      border: none;
      cursor: pointer;
      background-color: transparent; } }

@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 24px;
    right: 15px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd; } }

@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 30px;
    right: 12px;
    width: 19px;
    height: 1.5px;
    background-color: #011c40; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); } }

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none; }
  .main-nav--opened .main-nav__list {
    display: block;
    padding: 18px 15px;
    flex-direction: column;
    grid-column: 1 / 3; }
  .main-nav--opened {
    display: grid;
    row-gap: 10px;
    grid-template-columns: 1fr 1fr;
    align-content: flex-start;
    width: 285px;
    height: 568px;
    margin-left: auto;
    background-color: #f9fbfd; }
  .main-nav--opened .main-nav__link {
    padding: 14px 11px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40; }
  .main-nav--opened .main-nav__link--logo img {
    fill: #011c40; }
  .main-nav--opened .main-nav__link--logo {
    padding: 8px 15px 9px;
    grid-column: 1 / 2; }
  .main-nav--opened .main-nav__toggle {
    grid-column: 2 / 3; } }

.main__intro {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 120px 103px; }
  @media (max-width: 1365px) {
    .main__intro {
      width: 768px;
      margin: 0 auto;
      padding: 100px 45px; } }
  @media (max-width: 767px) {
    .main__intro {
      width: 320px;
      flex-direction: column;
      padding: 65px 15px; } }

.inner-header--background {
  margin: 0 auto; }
  @media (min-width: 1366px) {
    .inner-header--background {
      box-sizing: border-box;
      width: 100%;
      height: 650px;
      background-color: #011c40;
      background-image: url("../img/main-background-desktop.jpg");
      background-repeat: no-repeat;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); } }
  @media (min-width: 1366px) and (min-resolution: 2dppx) {
    .inner-header--background {
      background-image: url("../img/main-background-desktop@2x.jpg");
      background-size: cover; } }
  @media (max-width: 1365px) {
    .inner-header--background {
      max-width: 768px;
      height: 638px;
      background-image: url("../img/main-background-tablet.jpg");
      background-repeat: no-repeat;
      box-shadow: none; } }
  @media (max-width: 1365px) and (min-resolution: 2dppx) {
    .inner-header--background {
      background-image: url("../img/main-background-tablet@2x.jpg");
      background-size: contain; } }
  @media (max-width: 767px) {
    .inner-header--background {
      max-width: 320px;
      height: 500px;
      background-image: url("../img/main-background-mobile.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .inner-header--background {
      background-image: url("../img/main-background-mobile@2x.jpg");
      background-size: contain; } }

.inner-header__wrapper {
  max-width: 970px;
  padding: 283px 0 237px;
  margin: 0 auto; }
  @media (max-width: 1365px) {
    .inner-header__wrapper {
      max-width: 678px;
      padding: 280px 0 247px;
      margin: 0 auto; } }
  @media (max-width: 767px) {
    .inner-header__wrapper {
      max-width: 290px;
      padding: 177px 0 153px;
      margin: 0 auto; } }

.inner-header__wrapper h1 {
  margin: 0;
  margin-bottom: 30px;
  font-style: normal;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: #f9fbfd; }
  @media (max-width: 1365px) {
    .inner-header__wrapper h1 {
      margin-bottom: 29px;
      font-size: 50px;
      line-height: 61px; } }
  @media (max-width: 767px) {
    .inner-header__wrapper h1 {
      margin-bottom: 25px;
      font-size: 36px;
      line-height: 125%; } }

.inner-header__wrapper p {
  margin: 0;
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #f9fbfd; }
  @media (max-width: 1365px) {
    .inner-header__wrapper p {
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 10px; } }
  @media (max-width: 767px) {
    .inner-header__wrapper p {
      margin-bottom: 10px;
      font-size: 16px;
      line-height: 140%; } }

.intro__wrapper {
  max-width: 512px;
  padding: 66px 0 56px; }
  @media (max-width: 1365px) {
    .intro__wrapper {
      max-width: 324px;
      padding: 0; } }
  @media (max-width: 767px) {
    .intro__wrapper {
      max-width: 290px; } }

.intro__wrapper h2 {
  margin: 0;
  margin-bottom: 40px;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: #011c40; }
  @media (max-width: 1365px) {
    .intro__wrapper h2 {
      margin-bottom: 30px;
      font-size: 26px;
      line-height: 36px; } }
  @media (max-width: 767px) {
    .intro__wrapper h2 {
      font-size: 24px;
      line-height: 34px; } }

.intro__wrapper p {
  margin: 0;
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #011c40; }
  @media (max-width: 767px) {
    .intro__wrapper p {
      margin-bottom: 40px; } }

.intro img {
  width: 560px;
  height: 301px; }
  @media (max-width: 1365px) {
    .intro img {
      width: 324px;
      height: 264px; } }
  @media (max-width: 767px) {
    .intro img {
      width: 290px;
      height: 186px; } }

